import React, {useState, useRef, useEffect} from 'react';
import styled, { css } from 'styled-components/macro';
import gridOne from '../../src/images/house1.jpg';

const GridWrapper = styled.section`
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fill, 400px ) ;
    background-color: #fff;
    color: #444;
`;

const Box = styled.div`
    background-color: #444;
    color: #fff;
    border-radius: 5px;
    padding: 20px;
    font-size: 150%;
`;
const A = styled.div`
   grid-column: 1 / 3;
   background-image: URL("../../src/images/house1.jpg");
`;
const B = styled.div`
    grid-column: 3 / 5 ;
`;
const C = styled.div`
   grid-column: 1 / 2 ;
    grid-row: 2 ;
`;
const D = styled.div`
    grid-column: 2 / 5 ;
    grid-row: 2 ;
`;
const E = styled.div`
  grid-column: 1 / 5;
    grid-row: 3;
`;

const GridSection = ({  }) => {
    return (
        <div>
           <GridWrapper>
             <Box>
             <h1> Properties </h1>
             </Box>
              <Box>
                 <A>A
                 </A>
              </Box>
              <Box>
                <B> B </B>
              </Box>
              <Box>
                <C> C </C>
              </Box>
              <Box>
                <D> D </D>
              </Box>
              <Box>
                  <E>E </E>
              </Box>
              <Box>
                  <E>F </E>
              </Box> 
              <Box>
                  <E>G </E>
              </Box>
            
           
           </GridWrapper>

            </div>
           )

    }


export default GridSection